import React from "react"
import { Link } from "gatsby"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faClock } from "@fortawesome/free-regular-svg-icons"
import { faTag } from "@fortawesome/free-solid-svg-icons"

export default ({ date, tags }) => {
  return (
    <div className="columns mt-0 mb-0 is-vcentered" style={{'line-height': 1}}>
      <div className="column is-narrow pt-0">
        <div className="columns is-mobile is-vcentered">
          <div className="column is-narrow pr-0 is-flex">
            <span className="icon is-small mr-3"><FontAwesomeIcon icon={faClock} /></span>
          </div>
          <time className="column pl-0 is-narrow" dateTime={date}>
            <span>{date}</span>
          </time>
        </div>
      </div>
      {tags && (
        <div className="column pt-0">
          <div className="columns is-mobile is-vcentered">
            <div className="column is-narrow pr-0 is-flex">
              <span className="icon is-small mr-2"><FontAwesomeIcon icon={faTag} /></span>
            </div>
            <ul className="tags column pl-0">
              {tags.map((tag) => (
                <li className="tag is-light">
                  <Link to={`/tags/${tag}/`} className="has-text-dark">
                    {tag}
                  </Link>
                </li>
              ))}
            </ul>
          </div>
        </div>
      )}
    </div>
  )
}
