import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import DateAndTags from "../components/date_and_tags"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faAngleLeft, faAngleRight } from "@fortawesome/free-solid-svg-icons"

export default ({ data, pageContext, location }) => {
  const node = data.markdownRemark
  const { previous, next } = pageContext
  const nextNewer = next
  const nextOlder = previous

  return (
    <Layout>
      <SEO
        type={node.frontmatter.type}
        title={node.frontmatter.title}
        description={node.excerpt}
        eyecatch={node.frontmatter.eyecatch ? node.frontmatter.eyecatch.childImageSharp.original : null}
        pagePath={location.pathname}
      />
      <article className="card mb-6">
        <header>
          {node.frontmatter.eyecatch && (
            <div className="card-image">
              <figure className="image">
                <Img fluid={node.frontmatter.eyecatch.childImageSharp.fluid} alt={`eyecatch image of ${node.frontmatter.title}`} />
              </figure>
            </div>
          )}
          <div className="card-content pb-0">
            <DateAndTags
              date={node.frontmatter.date}
              tags={node.frontmatter.tags}
            />
            <h1 className="title mb-4">
              {node.frontmatter.title}
            </h1>
          </div>
        </header>
        <div className="card-content pt-0">
          <div dangerouslySetInnerHTML={{ __html: node.html }} className="content mt-4" />
        </div>
      </article>
      <nav className="pagination is-centered" role="navigation" aria-label="pagination">
        {nextNewer && (
          <Link to={nextNewer.fields.urlPath} className="pagination-previous">
            <div>
              <span className="heading">
                <FontAwesomeIcon icon={faAngleLeft} className="mr-2 has-text-grey-dark" />
                {nextNewer.frontmatter.date}
              </span>
              <span>{nextNewer.frontmatter.title}</span>
            </div>
          </Link>
        )}
        {nextOlder && (
          <Link to={nextOlder.fields.urlPath} className="pagination-next">
            <div>
              <span className="heading">
                {nextOlder.frontmatter.date}
                <FontAwesomeIcon icon={faAngleRight} className="ml-2 has-text-grey-dark" />
              </span>
              <span>{nextOlder.frontmatter.title}</span>
            </div>
          </Link>
        )}
      </nav>
    </Layout>
  )
}

export const pageQuery = graphql`
  query($urlPath: String!) {
    markdownRemark(fields: {urlPath: {eq: $urlPath}}) {
      fields {
        urlPath
      }
      html
      excerpt
      frontmatter {
        date(formatString: "YYYY-MM-DD")
        title
        tags
        eyecatch {
          childImageSharp {
            fluid(maxWidth: 1600) {
              ...GatsbyImageSharpFluid_withWebp
            }
            original {
              src
              width
              height
            }
          }
        }
      }
    }
  }
`
